import { Component, OnInit, ViewChild } from '@angular/core';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { PartParoduct } from '../../_models/PartParoduct';
import { Processor } from '../../_models/Processor';
import { SpectDetail } from '../../_models/SpectDetail';
import { Supplier } from '../../_models/Supplier';
import { PriceList } from '../../_models/PriceList';
import { RequirementService } from '../../_services/requirement.service';
import { AvgPriceList } from '../../_models/AvgPriceList';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';

@Component({
  selector: 'app-check-price',
  templateUrl: './check-price.component.html',
  styleUrls: ['./check-price.component.css']
})
export class CheckPriceComponent implements OnInit {
  model: any = {};
  araylength: any = 0;
  TavgPrice: any = 0;
  Tqty: any = 0;
  mymodel: any = {};
  roleid: any =  Number(String(localStorage.getItem('RoleID')));
  makes: PartParoduct[];
  models: PartParoduct[];
  processors: Processor[];
  hdds: SpectDetail[];
  rams: SpectDetail[];
  suppliers: Supplier[];
  prices: AvgPriceList[];
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
  };
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<AvgPriceList> = new Subject();
  public today: Date = new Date(new Date().toDateString());
  public weekStart: Date = new Date(new Date(new Date().setDate(new Date().getDate() - (new Date().getDay() + 7) % 7)).toDateString());
  public weekEnd: Date = new Date(new Date(new Date().setDate(new Date(new Date().setDate((new Date().getDate()
      - (new Date().getDay() + 7) % 7))).getDate() + 6)).toDateString());
  public monthStart: Date = new Date(new Date(new Date().setDate(1)).toDateString());
  public monthEnd: Date = this.today;
  public lastStart: Date = new Date(new Date(new Date(new Date().setMonth(new Date().getMonth() - 1)).setDate(1)).toDateString());
  public lastEnd: Date = new Date(new Date(new Date(new Date().setMonth(new Date().getMonth() - 1)).setDate(31)).toDateString());
  public yearStart: Date = new Date(new Date(new Date().setDate(new Date().getDate() - 365)).toDateString());
  public quateryearStart: Date = new Date(new Date(new Date().setDate(new Date().getDate() - 91)).toDateString());
  public midyearStart: Date = new Date(new Date(new Date().setDate(new Date().getDate() - 182)).toDateString());
  public yearEnd: Date = this.today;

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService,
    private requirementservice: RequirementService) { }

  ngOnInit(): void {
    this.loadMakes();
    this.loadProcessors();
    this.loadRAMs();
    this.loadHDDs();
    this.loadSuppliers();
  }
  loadMakes() {
    return this.loadspecsService.getMakes().subscribe((partproducts: PartParoduct[]) => {
      this.makes = partproducts.filter(proj => (proj.type === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadModels(value: number) {
    return this.loadspecsService.getModels().subscribe((partproducts: PartParoduct[]) => {
      this.models = partproducts.filter(proj => (Number(proj.parentId) === Number(value)));
      this.SearchAveragePrice(1);
    }, error => {
      this.alertify.error(error);
    });
  }
  loadProcessors() {
    return this.loadspecsService.getProcessors().subscribe((processors: Processor[]) => {
      this.processors = processors;
    }, error => {
      this.alertify.error(error);
    });
  }
  loadRAMs() {
    return this.loadspecsService.loadSpectDetails().subscribe((rams: SpectDetail[]) => {
      this.rams = rams.filter(proj => (proj.parentId === 1 ));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadHDDs() {
    return this.loadspecsService.loadSpectDetails().subscribe((hdds: SpectDetail[]) => {
      this.hdds = hdds.filter(proj => (proj.parentId === 2));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadSuppliers() {
    return this.loadspecsService.loadSuppliers().subscribe((suppliers: Supplier[]) => {
      this.suppliers = suppliers;
    }, error => {
      this.alertify.error(error);
    });
  }
  SearchAveragePrice(type: number) {
    this.mymodel.start = 'none';
    this.mymodel.end = 'none';
    if (this.mymodel.make_id && type === 1) {
      this.mymodel.model_id = 0;
      this.mymodel.proc_id = 0;
      this.mymodel.hdd_id = 0;
      this.mymodel.ram_id = 0;
      this.mymodel.supplier_id = 0;
      this.mymodel.type = 1;

    } else if (this.mymodel.model_id && type === 2) {

      this.mymodel.proc_id = 0;
      this.mymodel.hdd_id = 0;
      this.mymodel.ram_id = 0;
      this.mymodel.supplier_id = 0;
      this.mymodel.type = 2;

    } else if (this.mymodel.proc_id && type === 3) {

      this.mymodel.hdd_id = 0;
      this.mymodel.ram_id = 0;
      this.mymodel.supplier_id = 0;
      this.mymodel.type = 3;

    } else if (this.mymodel.hdd_id && type === 4) {

      this.mymodel.ram_id = 0;
      this.mymodel.supplier_id = 0;
      this.mymodel.type = 4;

    } else if (this.mymodel.ram_id && type === 5) {

      this.mymodel.supplier_id = 0;
      this.mymodel.type = 5;

    } else if (this.mymodel.supplier_id && type === 6) {

      this.mymodel.type = 6;

    }
    if (this.mymodel.make_id && type === 1 || this.mymodel.model_id && type === 2 || this.mymodel.proc_id && type === 3
      || this.mymodel.hdd_id && type === 4 || this.mymodel.ram_id && type === 5 || this.mymodel.supplier_id && type === 6) {
      this.requirementservice.searchAveragePrice(this.mymodel).subscribe((prices: AvgPriceList[]) => {
        this.prices = prices;
        this.araylength = prices.length;
        let sum = 0;
        let qty = 0;
        prices.forEach(function (value) {
          sum += parseFloat(value.avg_price) * value.qty ;
          qty += value.qty;
        });
        this.TavgPrice = sum / qty;
        this.Tqty = qty;
        if (this.isDtInitialized) {
          this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
          });
        } else {
          this.isDtInitialized = true;
          this.dtTrigger.next();
        }
      }, error => {
        this.alertify.error(error);
      });
    }
  }

  SearchAveragePriceWithdaterange(e) {
     if (e.text) {
      this.mymodel.start = e.text.split(' - ', 3)[0];
      this.mymodel.end = e.text.split(' - ', 3)[1];
     } else {
      this.mymodel.start = 'none';
      this.mymodel.end = 'none';
     }
     if (!this.mymodel.model_id) {
      this.mymodel.model_id = 0;
     }
    if (this.mymodel.make_id) {
      this.requirementservice.searchAveragePrice(this.mymodel).subscribe((prices: AvgPriceList[]) => {
        this.prices = prices;
        let sum = 0;
        let qty = 0;
        prices.forEach(function (value) {
          sum += parseFloat( value.avg_price) * value.qty ;
          qty += value.qty;
        });
        this.TavgPrice = sum / qty;
        this.Tqty = qty;
        if (this.isDtInitialized) {
          this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
          });
        } else {
          this.isDtInitialized = true;
          this.dtTrigger.next();
        }
      }, error => {
        console.log(error);
        this.alertify.error(error);
      });
    }
  }

}
